<template>
  <div class="card shadow-sm mb-4">
    <div class="card-header bg-white d-flex justify-content-between align-items-center">
      <h5 class="mb-0">üìß ÈÇÆÁÆ±ÁªëÂÆö</h5>
      <span v-if="emailVerified" class="badge bg-success">
        ‚úÖ Â∑≤È™åËØÅ
      </span>
    </div>
    
    <div class="card-body">
      <!-- ÂΩìÂâçÈÇÆÁÆ±ÊòæÁ§∫ -->
      <div v-if="currentEmail && emailVerified" class="mb-3">
        <div class="alert alert-success">
          <strong>Â∑≤ÁªëÂÆöÈÇÆÁÆ±Ôºö</strong> {{ currentEmail }}
        </div>
      </div>
      
      <!-- Êú™ÁªëÂÆöÊàñÊú™È™åËØÅÊèêÁ§∫ -->
      <div v-else-if="!emailVerified" class="mb-3">
        <div class="alert alert-warning">
          <strong>‚ö†Ô∏è ÈÇÆÁÆ±{{ currentEmail ? 'Êú™È™åËØÅ' : 'Êú™ÁªëÂÆö' }}</strong>
          <p class="mb-0 small">ÁªëÂÆöÈÇÆÁÆ±ÂèØ‰ª•Áî®‰∫éÊâæÂõûÂØÜÁ†ÅÂíåÊé•Êî∂ÈáçË¶ÅÈÄöÁü•</p>
        </div>
      </div>
      
      <!-- ÈÇÆÁÆ±ÁªëÂÆöË°®Âçï -->
      <div v-if="!emailVerified || !currentEmail">
        <form @submit.prevent="handleBindEmail">
          <!-- ÈÇÆÁÆ±ËæìÂÖ• -->
          <div class="mb-3">
            <label class="form-label">üìß ÈÇÆÁÆ±Âú∞ÂùÄ <span class="text-danger">*</span></label>
            <div class="input-group">
              <input
                type="email"
                class="form-control"
                v-model="emailForm.email"
                :disabled="emailVerified && currentEmail"
                placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±Âú∞ÂùÄ"
                required
              />
              <button
                v-if="!emailVerified || !currentEmail"
                type="button"
                class="btn btn-outline-primary"
                :disabled="!emailForm.email || sendingCode || countdown > 0 || verifyingCode"
                @click="handleSendCode"
              >
                <span v-if="sendingCode" class="spinner-border spinner-border-sm me-1"></span>
                <span v-else-if="countdown > 0">{{ countdown }}Áßí</span>
                <span v-else>ÂèëÈÄÅÈ™åËØÅÁ†Å</span>
              </button>
            </div>
            <small v-if="codeSent && !emailVerified" class="text-success">
              È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÔºåËØ∑Êü•Êî∂ÈÇÆÁÆ±
            </small>
          </div>
          
          <!-- È™åËØÅÁ†ÅËæìÂÖ• -->
          <div class="mb-3" v-if="codeSent">
            <label class="form-label">üîê È™åËØÅÁ†Å <span class="text-danger">*</span></label>
            <input
              type="text"
              class="form-control"
              v-model="emailForm.code"
              :disabled="emailVerified"
              placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å"
              maxlength="6"
              required
            />
          </div>
          
          <!-- Êìç‰ΩúÊåâÈíÆ -->
          <div class="d-flex gap-2" v-if="codeSent && !emailVerified">
            <button
              type="submit"
              class="btn btn-primary"
              :disabled="binding || !emailForm.code || emailForm.code.length !== 6"
            >
              <span v-if="binding" class="spinner-border spinner-border-sm me-2"></span>
              {{ binding ? 'ÁªëÂÆö‰∏≠...' : '‚úÖ ÁªëÂÆöÈÇÆÁÆ±' }}
            </button>
            <button
              type="button"
              class="btn btn-outline-secondary"
              @click="handleCancel"
              :disabled="binding"
            >
              ÂèñÊ∂à
            </button>
          </div>
        </form>
      </div>
      
      <!-- ÈîôËØØÊèêÁ§∫ -->
      <div v-if="errorMessage" class="alert alert-danger mt-3">
        {{ errorMessage }}
      </div>
      
      <!-- ÊàêÂäüÊèêÁ§∫ -->
      <div v-if="successMessage" class="alert alert-success mt-3">
        {{ successMessage }}
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { sendVerificationCode, verifyCode } from '@/api/auth'
import { bindEmail } from '@/api/user'

export default {
  name: 'EmailBindingEditor',
  
  props: {
    currentEmail: {
      type: String,
      default: ''
    },
    emailVerified: {
      type: Boolean,
      default: false
    },
    userId: {
      type: Number,
      required: true
    }
  },
  
  emits: ['email-bound', 'update'],
  
  setup(props, { emit }) {
    const emailForm = ref({
      email: props.currentEmail || '',
      code: ''
    })
    
    const sendingCode = ref(false)
    const binding = ref(false)
    const codeSent = ref(false)
    const countdown = ref(0)
    const errorMessage = ref('')
    const successMessage = ref('')
    
    let countdownTimer = null
    
    // ÂèëÈÄÅÈ™åËØÅÁ†Å
    const handleSendCode = async () => {
      errorMessage.value = ''
      successMessage.value = ''
      
      if (!emailForm.value.email) {
        errorMessage.value = 'ËØ∑ÂÖàËæìÂÖ•ÈÇÆÁÆ±Âú∞ÂùÄ'
        return
      }
      
      // È™åËØÅÈÇÆÁÆ±Ê†ºÂºè
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
      if (!emailRegex.test(emailForm.value.email)) {
        errorMessage.value = 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÈÇÆÁÆ±Âú∞ÂùÄ'
        return
      }
      
      sendingCode.value = true
      
      try {
        await sendVerificationCode({
          email: emailForm.value.email,
          type: 'bind_email'
        })
        
        codeSent.value = true
        countdown.value = 60 // 60ÁßíÂÄíËÆ°Êó∂
        startCountdown()
        successMessage.value = 'È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÔºåËØ∑Êü•Êî∂ÈÇÆÁÆ±'
        
      } catch (error) {
        console.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', error)
        if (error.response?.data) {
          const data = error.response.data
          if (data.error) {
            errorMessage.value = data.error
          } else if (data.email) {
            errorMessage.value = Array.isArray(data.email) ? data.email[0] : data.email
          } else {
            errorMessage.value = 'ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
          }
        } else {
          errorMessage.value = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•'
        }
      } finally {
        sendingCode.value = false
      }
    }
    
    // ÁªëÂÆöÈÇÆÁÆ±
    const handleBindEmail = async () => {
      errorMessage.value = ''
      successMessage.value = ''
      
      if (!emailForm.value.code || emailForm.value.code.length !== 6) {
        errorMessage.value = 'ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å'
        return
      }
      
      binding.value = true
      
      try {
        // 1. ÂÖàÈ™åËØÅÈ™åËØÅÁ†Å
        const verifyResponse = await verifyCode({
          email: emailForm.value.email,
          code: emailForm.value.code,
          type: 'bind_email'
        })
        
        if (!verifyResponse.success || !verifyResponse.verification_token) {
          errorMessage.value = 'È™åËØÅÁ†ÅÈ™åËØÅÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•È™åËØÅÁ†ÅÊòØÂê¶Ê≠£Á°Æ'
          binding.value = false
          return
        }
        
        // 2. ÁªëÂÆöÈÇÆÁÆ±Ôºà‰ΩøÁî®‰∏ìÈó®ÁöÑÁªëÂÆöÈÇÆÁÆ±APIÔºå‰ºöËá™Âä®Ê†áËÆ∞‰∏∫Â∑≤È™åËØÅÔºâ
        await bindEmail({
          email: emailForm.value.email,
          verification_token: verifyResponse.verification_token
        })
        
        // 3. Ëß¶ÂèëÊõ¥Êñ∞‰∫ã‰ª∂
        emit('email-bound', {
          email: emailForm.value.email,
          verified: true
        })
        emit('update')
        
        successMessage.value = 'ÈÇÆÁÆ±ÁªëÂÆöÊàêÂäüÔºÅ'
        
        // Ê∏ÖÈô§Ë°®Âçï
        emailForm.value.code = ''
        codeSent.value = false
        if (countdownTimer) {
          clearInterval(countdownTimer)
          countdownTimer = null
          countdown.value = 0
        }
        
      } catch (error) {
        console.error('ÁªëÂÆöÈÇÆÁÆ±Â§±Ë¥•:', error)
        if (error.response?.data) {
          const data = error.response.data
          if (data.error) {
            errorMessage.value = data.error
          } else if (data.code) {
            errorMessage.value = Array.isArray(data.code) ? data.code[0] : data.code
          } else if (data.email) {
            errorMessage.value = Array.isArray(data.email) ? data.email[0] : data.email
          } else {
            errorMessage.value = 'ÁªëÂÆöÈÇÆÁÆ±Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
          }
        } else {
          errorMessage.value = 'ÁΩëÁªúÈîôËØØÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•'
        }
      } finally {
        binding.value = false
      }
    }
    
    // ÂèñÊ∂àÁªëÂÆö
    const handleCancel = () => {
      emailForm.value.code = ''
      codeSent.value = false
      errorMessage.value = ''
      successMessage.value = ''
      if (countdownTimer) {
        clearInterval(countdownTimer)
        countdownTimer = null
        countdown.value = 0
      }
    }
    
    // ÂÄíËÆ°Êó∂
    const startCountdown = () => {
      if (countdownTimer) {
        clearInterval(countdownTimer)
      }
      
      countdownTimer = setInterval(() => {
        if (countdown.value > 0) {
          countdown.value--
        } else {
          clearInterval(countdownTimer)
          countdownTimer = null
        }
      }, 1000)
    }
    
    return {
      emailForm,
      sendingCode,
      binding,
      codeSent,
      countdown,
      errorMessage,
      successMessage,
      handleSendCode,
      handleBindEmail,
      handleCancel
    }
  }
}
</script>

<style scoped>
.card {
  border-radius: 12px;
  border: 1px solid #e9ecef;
}

.card-header {
  border-bottom: 2px solid #f0f0f0;
  padding: 1rem 1.5rem;
}

.form-label {
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.form-control {
  border-radius: 12px;
  border: 2px solid #e9ecef;
  padding: 0.75rem 1rem;
  transition: all 0.3s ease;
}

.form-control:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 0.2rem rgba(102, 126, 234, 0.25);
}

.input-group {
  display: flex;
  gap: 0.5rem;
}

.input-group .form-control {
  flex: 1;
}

.input-group .btn {
  white-space: nowrap;
}

.d-flex.gap-2 {
  gap: 0.5rem;
}

.alert {
  border-radius: 12px;
  padding: 1rem;
}

.badge {
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.85rem;
}
</style>

